<template>
  <div style="background-color: black;width: 100%; height:100%;"
    class="d-flex align-items-center justify-content-center">
    <div class="my-video-container">{{ this.height }} {{ this.width }}</div>
  </div>
</template>

<script>
/* eslint-disable */
import * as tf from '@tensorflow/tfjs';
import PhotosGallery from "./PhotosGallery.vue";
import { computed, onMounted, onUnmounted, ref } from "vue"

export default {
  setup() {
    const dina5Ratio = 210 / 148

    let windowWidth = ref(window.innerWidth)
    let windowHeight = ref(window.innerHeight)

    const onResize = () => {
      windowWidth.value = window.innerWidth;
      windowHeight.value = window.innerHeight;
    }
    onMounted(() => window.addEventListener('resize', onResize))
    onUnmounted(() => window.removeEventListener('resize', onResize))

    const width = computed(() => {
      if (windowHeight.value / windowWidth.value < dina5Ratio) {
        return windowHeight.value / dina5Ratio + "px"
      } else {
        return windowWidth.value + "px"
      }
    });
    const height = computed(() => {
      if (windowHeight.value / windowWidth.value < dina5Ratio) {
        return windowHeight.value + "px"
      } else {
        return windowWidth.value * dina5Ratio + "px"
      }

    })

    return { width, height }
  },
  components: {
    PhotosGallery,
  },
  data() {
    return {
      videoContainerHeight: (30 + 60) + "%",
      // containerWidth: this.width,
      // containerHeight: this.height,
    };
  },
  methods: {
    async mounted() {
    },
  }
}
</script>

