<template>

  <!-- DATA VIEW-->
  <div ref="datawrapper">


    <MDBContainer>
      <MDBRow>
        <MDBCol md="12" class="mx-auto">
          <MDBRow>
            <img class="mx-auto my-4" style="max-width: 500px" src="../assets/pollenyzer_logo.svg" />
          </MDBRow>

          <MDBRow> </MDBRow>
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <Transition appear name="bounce">
          <p v-if="showCards[0]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardText>
                  <v-zoomer style="width: 100%; border: solid 1px silver;">
                    <!-- <img src="@/assets/testbild.jpg" style="object-fit: contain; width: 100%; height: 100%;"> -->
                    <canvas ref="annotationcanvas" class="annotation-canvas"></canvas>

                  </v-zoomer>
                </MDBCardText>

              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
        <Transition appear name="bounce" @after-enter="showForceGraph()">
          <p v-if="showCards[1]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardText>
                  <div ref="forcegraph" style="width:100%; height: 400px"></div>

                </MDBCardText>

              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
        <Transition appear name="bounce" @after-enter="showClusters(this.nCluster)">
          <p v-if="showCards[2]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardText>
                  <div ref="barcanvascontainer">
                    <canvas ref="barcanvas" class="bar-canvas"></canvas>
                  </div>
                  <div class="m-3">
                    <vue3-slider trackColor="#ffa820" color="#97be11" :tooltip="true" @drag-end="showClusters"
                      v-model="nCluster" :handleScale="2.1" :min="1" :alwaysShowHandle="true" :sticky="true"
                      :max="maxClusters" :disabled="inProgress" />
                  </div>
                </MDBCardText>

              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
      </MDBRow>
    </MDBContainer>
    <CamFooter @nextCard="nextCard()" @previousCard="previousCard()" @redo="StartRecording()"
      :prevDisabled="prevDisabled" :nextDisabled="nextDisabled" :redoDisabled="redoDisabled" />
  </div>
</template>

<style>
.annotation-canvas {
  width: 100%;
}

.bar-canvas {
  width: 100%;
}
</style>

<script>
/* eslint-disable */
import {
  MDBCol, MDBRow, MDBContainer, MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
} from "mdb-vue-ui-kit";
import { computed, onMounted, onUnmounted, ref } from "vue"
import CamFooter from '@/components/CamFooter.vue'
import ForceGraph from 'force-graph';
import { clusterData } from '@greenelab/hclust';
import { euclideanDistance } from '@greenelab/hclust';
import { avgDistance } from '@greenelab/hclust';
import slider from "vue3-slider"
import {
  Chart,
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip,
  SubTitle
} from 'chart.js';
Chart.register(
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip,
  SubTitle
);


export default {
  components: {
    MDBCol,
    MDBRow,
    //MyCardControls,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    CamFooter,
    "vue3-slider": slider,
  },
  props: {
    pollenData: Object,
  },
  data() {
    return {
      showCards: [true, false, false],
      cardId: 0,
      maxClusters: 2,
      nCluster: 1,
      barChart: null,
      inProgress: false
    }
  },
  mounted() {
    this.showAnnotations()
  },
  watch: {
    pollenData(value, oldValue) {
      this.showAnnotations(value)
    }
  },
  methods: {
    async showClusters(K) {
      console.log("show clusters wir K=", K)
      if (this.inProgress) {
        return
      }
      this.inProgress = true;
      let lab_colors = this.pollenData.pollen.annotations.lab_colors
      let rgb_colors = this.pollenData.pollen.annotations.rgb_colors
      let str_colors = this.pollenData.pollen.annotations.str_colors
      this.maxClusters = rgb_colors.length
      console.log("clusters", lab_colors)
      console.log(lab_colors[0])
      let clusterResult = clusterData({
        data: lab_colors
      });
      console.log(clusterResult)


      let barChartData = []
      //const K = 2
      console.log(clusterResult.clustersGivenK)
      let n_cluster = clusterResult.clustersGivenK[K].length

      for (let clusterId = 0; clusterId < n_cluster; clusterId++) {
        clusterResult.clustersGivenK[K][clusterId].forEach((pollenId) => {
          console.log("pollenID", pollenId)
          let data = new Array(n_cluster).fill(null)
          data[clusterId] = 1
          barChartData.push({
            backgroundColor: (
              "rgba(" +
              rgb_colors[pollenId][0] + "," +
              rgb_colors[pollenId][1] + "," +
              rgb_colors[pollenId][2] + "," +
              1.0 + ")"
            ),
            // borderColor: (
            //   "rgb(" +
            //   rgb_colors[pollenId][0] + "," +
            //   rgb_colors[pollenId][1] + "," +
            //   rgb_colors[pollenId][2] + ")"
            // ),
            borderWidth: 1,
            data: data
          })

        })
      }
      console.log(barChartData)


      let barCanvas = this.$refs.barcanvas
      let barCanvasContainer = this.$refs.barcanvascontainer;
      barCanvas.remove();
      barCanvasContainer.innerHTML = "<canvas ref='barcanvas' id='barcanvas' class='bar-canvas'></canvas>"
      barCanvas = document.getElementById("barcanvas")

      const ctx = barCanvas.getContext('2d');
      if (this.barChart) {
        this.barChart.destroy()
      }
      this.barChart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: [...(new Array(K)).keys()].map((v) => { return "" }),
          datasets: barChartData
        },
        options: {
          plugins: {
            tooltip: {
              enabled: false
            },
            legend: {
              display: false,
            },
            title: {
              display: true,
              text: rgb_colors.length + " pollen in " + K + " clusters"
            }
          },
          scales: {
            x: {
              stacked: true,
            },
            y: {
              stacked: true
            },
          },
          responsive: true,
          //legend: { position: 'bottom' },
        }
      });

      await setTimeout(() => {
        this.inProgress = false
      }, 1200);

    },
    showForceGraph() {
      console.log(this.pollenData.pollen.force_graph)
      let forcegraph = this.$refs.forcegraph;
      const Graph = ForceGraph()
        (forcegraph)
        .width(forcegraph.clientWidth)
        .height(forcegraph.clientHeight)
        .cooldownTicks(200)
        .graphData(this.pollenData.pollen.force_graph)

      Graph.d3Force('center', null);

      // fit to canvas when engine stops
      Graph.onEngineStop(() => Graph.zoomToFit(400));
    },
    showAnnotations(pollenData) {
      if (pollenData) {
        let annotationCanvas = this.$refs.annotationcanvas
        const context = annotationCanvas.getContext('2d');
        console.log("DATAT", pollenData)
        context.canvas.width = pollenData.imgWidth
        context.canvas.height = pollenData.imgHeight
        var image = new Image();
        image.onload = function () {
          context.drawImage(image, 0, 0, pollenData.imgWidth, pollenData.imgHeight);

          for (let i = 0; i < pollenData.pollen.annotations.coords.length; i++) {
            let coord = pollenData.pollen.annotations.coords[i]
            let color = pollenData.pollen.annotations.str_colors[i]
            const centerX = coord.x;
            const centerY = coord.y;
            const radius = annotationCanvas.clientWidth * 0.008;
            console.log(centerX, centerY, color)

            context.beginPath();
            context.arc(centerX, centerY, radius, 0, 2 * Math.PI, false);
            context.fillStyle = color;
            context.fill();
            // context.lineWidth = 0;
            // context.strokeStyle = '#000';
            // context.stroke();
          }

        };
        image.src = pollenData.dataURL
      }
    },
    nextCard() {
      this.showCards[this.cardId] = false;
      this.cardId++;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
      if (this.cardId == 0) {
        this.showAnnotations(this.pollenData)
      } else if (this.cardId == 1) {
        //setTimeout(this.showForceGraph(), 2000)

      }
    },
    previousCard() {
      this.showCards[this.cardId] = false;
      this.cardId--;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
      if (this.cardId == 0) {
        this.showAnnotations(this.pollenData)
      } else if (this.cardId == 1) {
        this.showForceGraph()
      }

    },
  },
};
</script>


